*{
  background-color: darkslategray;
}

.Form{
  display: flex;
  align-items: center;
  justify-content: center ;
}

.inputText{
  display: flex;
  margin-left: auto;
  margin-right: auto;
  height: 40px;
  width: 200px;
  margin-top: 40px;
  font-size: 1.3rem;
  color:deeppink
}

.Poster{
  background-color: lavender;
  display: flex;
 
  height: 800px;
  margin-top: 50px;
  width: 600px;
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  flex-direction: column;
}

.GorenDiv{
  position: relative;
  background-color: inherit;
}

p{
  background-color: inherit;
}

.CityCountry{
  position: absolute;
  top: 10px;
  right: 180px;
  width: max-content;
  font-size: 1.3rem;
  font-weight: bold;
}

.mainTemp{
  position: absolute;
  top:85px;
  right: 200px;
  width: max-content;
  font-size: 2.5rem;
  color:deeppink;
}

.feelsLikeTemp{
  position: absolute;
  top: 160px;
  right: 170px;
  width: max-content;
  font-size: 1.2rem;

}

.WindHumidity{
  display: flex;
  flex-direction: row;
  position: absolute;
  top: 240px;
  right: 0;
  width: max-content;
  background-color: inherit;
}
.Wind, .Humidity {
  background-color: inherit;
  padding-right: 30px;
}


.nemaGrad{
  display: flex;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  flex-direction: column;
  color:deeppink;
  font-size: 2rem;
  font-weight: bold;

}

